body {
  width: 790px;
  padding: 0;
  margin: 0 auto;
  background-color: white;
}

.page {
  page-break-after: always;
  position: relative;
}

.page-details {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10000;
}

.form-img {
  width: 100%;
}

.answer {
  position: absolute;
  bottom: 0;
}

.answer img {
  width: 100%;
  position: absolute;
}
.answer input,
.answer textarea {
  width: 100%;
  font-size: 10px;
  font-weight: bold;
  color: #141414;
  position: absolute;
  background: transparent;
  overflow: hidden;
}

.answer textarea {
  font-size: 10px;
  padding: 0 5px;
}

.answer .check-mark {
  position: absolute;

  &::before,
  &::after {
    content: "";
    height: 2px;
    width: 15px;
    background: black;
    position: absolute;
    transform: rotate(45deg);
  }
  &::before {
    transform: rotate(-45deg);
  }
}
textarea.form-control {
  border: 0;
  resize: none;
  height: 100%;
}

